.map__image {
  position: relative;
  width: 100%;
  height: 462px; }
  .map__image picture {
    position: absolute;
    top: 0;
    left: 0;
    bottom: auto;
    right: auto;
    width: 100%;
    height: 100%; }

.map__element {
  position: relative; }
  .map__element::before {
    position: absolute;
    top: -1px;
    left: -24px;
    bottom: auto;
    right: auto;
    content: "";
