    height: 16px;
    box-sizing: border-box;
    background-color: #2c36f2;
    border-radius: 2px;
    cursor: pointer; }

input[type="checkbox"]:checked + .map__element::before {
  background-image: url("../img/check_mark--white.svg");
  background-position: 50% 50%;
  background-size: 7px;
  background-repeat: no-repeat; }

